
.container {
  width: auto;
  margin-left: 3%;
  margin-right: 3%;
  padding: 0px, 0px, 0px, 0px;
  /* margin-left: 30px;
  margin-right: 30px;  */
  /* background-color: green; */
  /* transition: transform .2s; */
}

.container:hover { /* IE 9 */
  -webkit-transform: scale(1.08); /* Safari 3-8 */
  transform: scale(1.08); 
}

.card {
  background: #fff;
  border-radius: 2px;
  height: 165px;
  margin: 0.5rem;
  padding: 0.25rem;
  position: relative;
  width: 165px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
  background:#6CADDC;
}

.card > .img-container > img {
  width: 80%
}

.card > .content {
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 15px;
}

.card > .content > ul {
  list-style-type: none;
}

.card > .content > ul > li {
  margin-top: 18px;
}

/* main.container {
  margin: 0px;
} */

.column {
  float: left;
  width: 25%;
  padding: 5px;
}

/* Clearfix (clear floats) */
.row::after {
  content: "";
  clear: both;
  display: table;
}


